.characterEditor {
  padding-top: 128px;
  padding-bottom: 64px;
  isolation: isolate;
}

.maxWidthWrapper {
    position: relative;
    z-index: 1;
}

.header {
  padding-bottom: 64px;
  width: 65%;
}

.title {
  font-size: 2.5rem;
  font-weight: 600;
  margin-top: 0;
  margin-bottom: 16px;
  letter-spacing: -0.5px;
}

.description {
  font-size: 1.25rem;
  margin: 0;
}

.characterWrapperWrapper {
    position: fixed;
    top: 128px;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    max-width: 960px;
    margin: auto;
}

.characterWrapper {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 35%;
}

.characterWrapper svg {
    position: absolute;
    inset: 0;
    margin: auto;
    min-height: 500px;
}

.controlColumn {
    width: 50%;
}

.perspective {
    background: hsl(195deg, 20%, 86%);
    height: 40vh;
    position: fixed;
    right: 0;
    left: 0;
    bottom: 0;
}

@media only screen and (max-width: 480px) {
    .characterEditor {
        padding-top: 64px;
    }

    .header {
        width: 100%;
    }

    .characterWrapperWrapper {
        position: relative;
        transform: translateY(-350px);
    }

    .characterWrapper {
        width: auto;
        inset: 0;
    }

    .controlColumn {
        display: flex;
        flex-direction: row;
        overflow: auto;
        width: auto;
        margin-top: 250px;
        margin-left: -32px;
        margin-right: -32px;
        padding: 0 32px;
        gap: 16px;
    }

    .perspective {
        display: none;
    }
}
